@use '../styles/mixins' as *;
@use '../styles/variables' as *;

.downloadsSection {
    background: rgba(0, 0, 0, 0.1);
    padding-bottom: $spacing-xxl * 2; // Extra space at bottom
}

.sectionTitle {
    text-align: center;
    margin-bottom: $spacing-xl;
}

.grid {
    display: flex;
    justify-content: center;
    gap: $spacing-lg;
    flex-wrap: wrap;
}

.downloadCard {
    @include glass(0.05, 10px);
    @include card-hover;
    padding: $spacing-xl;
    border-radius: $radius-md;
    width: 250px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: $spacing-sm;
    color: $color-text;

    &:hover {
        border-color: $color-primary;

        .icon {
            transform: scale(1.1) rotate(5deg);
        }

        .linkText {
            color: $color-primary;
        }
    }
}

.icon {
    font-size: 3rem;
    margin-bottom: $spacing-sm;
    transition: transform 0.3s ease;
}

.linkText {
    font-size: 0.9rem;
    color: $color-text-muted;
    margin-top: auto;
    transition: color 0.3s ease;
}