@use '../styles/mixins' as *;
@use '../styles/variables' as *;

.hero {
    min-height: 80vh;
    @include flex-center;
    padding-top: $spacing-xxl;
}

.heroContent {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: $spacing-xl;

    @include tablet {
        flex-direction: column-reverse;
        text-align: center;
    }
}

.textContent {
    flex: 1;
}

.greeting {
    display: block;
    font-size: 1.25rem;
    color: $color-primary;
    margin-bottom: $spacing-xs;
    font-weight: 500;
}

.name {
    @include gradient-text;
    margin-bottom: $spacing-xs;
}

.role {
    color: $color-text-muted;
    font-size: 1.5rem;
    margin-bottom: $spacing-md;

    @include tablet {
        font-size: 1.25rem;
    }
}

.description {
    max-width: 500px;
    font-size: 1.1rem;
    color: $color-text;

    @include tablet {
        margin: 0 auto;
    }
}

.imageContent {
    flex: 1;
    @include flex-center;
}

.imagePlaceholder {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.05);
    border: 2px solid $color-primary;
    @include flex-center;
    position: relative;
    overflow: hidden;
    box-shadow: 0 0 50px rgba($color-primary, 0.2);

    span {
        color: $color-text-muted;
    }

    &::before {
        content: '';
        position: absolute;
        top: -50%;
        left: -50%;
        width: 200%;
        height: 200%;
        background: radial-gradient(circle, rgba($color-primary, 0.1) 0%, transparent 70%);
        animation: rotate 10s linear infinite;
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}